<template>
  <div id="app">
    <SocialLinks />
    <AnimatedAlbum :class="{'hide': isClicked}" />
    <div id="date" :class="{'selected': isClicked}" @click="clickHandler">
      <h1 data-text="May 26">May 26</h1>
    </div>
    <p :class="{'show-text': isClicked, 'readable': isReadable}">
      <span @click="isReadable = !isReadable" class="readable-link">I wake up,</span>
      questioning if I exist. Fingers, trailing in the dark, grasp at my eyes. Yep. I guess those are real. My ears wince at the sound of the watch beside the bed and I wonder which of its stuttering hands I am today. Will I crawl through each hour, counting out passed digits and letting them blur with the remainders? Or will I dash quickly around the house, every movement a loud proclamation of what I've done and where I'm going. Often the latter ends in being far too busy; a distraction that obfuscates any notice for the sneaking ritard of my winding. I think that's who I am today: not wound. Or worn out. Today I find it is much too difficult for my hands to pull from my face. I bet the creases in my arms are growing tired of waiting to be stretched taut again. If only my limbs would move by themselves as they sometimes do. Thank fucking god for times like those. The less energy my body uses for thinking, the more there is to siphon away for its half-assed attempts at doing.
    </p>
  </div>
</template>

<script>
import AnimatedAlbum from "./components/AnimatedAlbum.vue";
import SocialLinks from "./components/SocialLinks";
import Narrative from "./components/Narrative";

export default {
  name: "app",
  components: {
    AnimatedAlbum,
    SocialLinks,
    Narrative
  },
  data() {
    return {
      isClicked: false,
      isReadable: false
    };
  },
  methods: {
    clickHandler() {
      this.isClicked = !this.isClicked;
    }
  }
};
</script>


<style lang="scss">
@import url("https://fonts.googleapis.com/css?family=Knewave|Raleway:900&display=swap");

.hide#album-art {
