}
h1 {
  color: white;
  font-size: 56px;
  text-transform: uppercase;
  opacity: 0.7;
  filter: blur(5px);
  margin-top: 0;
  animation: fade 30s;

  &:hover {
    cursor: pointer;
  }
}

#date.selected {
